<template>
<transition name="slide-fade">
	<div class="cc-main">
		<img class="cc-logo" src="../assets/img/bg.png" alt="">
		<div class="cc-jieshao" @click="goto('aboutus')">平台介绍</div>
		<div class="cc-register">
			<div class="cc-register1">
				<div class="cc-tit">劳务经纪人基本信息</div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="plname" placeholder="请输入您的姓名" label="联系人" /></div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="pltel" type="tel" clearable placeholder="请输入手机号码" label="手机号码" /></div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="pladdr" placeholder="请选择所在地区" label="所在地区" readonly right-icon="arrow" @click="plshow = true" /></div>
				<van-field class="cc-input" label-width="84px" v-model="plenterprise" placeholder="选填" label="机构名称" />
			</div>
			<div class="cc-register1">
				<div class="cc-tit">劳务输出人员信息</div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="plnum" type="number" placeholder="需要输出的人员数量" label="劳务人数" /></div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="plprice" placeholder="请输入员工的薪资要求" label="薪资要求" /></div>
				<van-field class="cc-input" label-width="84px" v-model="plrequire" type="textarea" :autosize="{maxHeight:62}" placeholder="其他说明" label="其他" />
				<div class="cc-tit1">上传名单（图片、文档）</div>
				<div class="cc-upload1 flexstart">
					<div class="cc-uploaded" v-show="filedata!=''">
						<img @click="del" class="cc-del" src="../assets/img/del.png" alt="">
						<img src="../assets/img/txt.png" alt="">
					</div>
					<div class="cc-uploadicon" v-show="filedata==''">
						<van-icon class="cc-icon" name="plus" />
						<p>添加文件</p>
						<input type="file" id="people-export" ref="inputer" @change="afterRead"/>
					</div>
				</div>
			</div>
			<div class="cc-button" @click="sub">确认登记</div>
		</div>
		<img class="cc-logo1" src="../assets/img/logo1.png" alt="">
		<van-popup v-model="plshow" position="bottom"><van-area :area-list="areaList" :value="pldistrictCode" visible-item-count="9" @confirm="plconfirmarea" @cancel="plcancelarea" /></van-popup>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Area ,Popup ,Field ,Icon,Dialog,Toast} from 'vant';
import areaList from '@/assets/js/area.js'//基础样式
export default {
	name: 'home1',
	components: {
		[Area .name]: Area ,
		[Field.name]: Field,
		[Popup.name]: Popup,
		[Icon.name]: Icon,
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
	},
	data() {
		return {
			areaList,
			plshow:false,
			plenterprise:"",
			plname:"",
			pltel:"",
			pldistrictCode:'341202',
			plprovince:"安徽省",
			plcity:"阜阳市",
			pldistrict:"颍州区",
			pladdr:"安徽省阜阳市颍州区",
			plnum:"",
			plprice:"",
			plrequire:"",
			filedata:"",
		};
	},
	mounted() {
		this.getIntermediaryInfo();
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/"+rt
			})
		},
		//渲染数据
		getIntermediaryInfo(){
			var that=this;
			this.$http.get('/main/getIntermediaryInfo').then(function (res) {
				if (res.code==10000) {
					if (res.data.intermediary) {
						that.plenterprise=res.data.intermediary.name;
						that.plname=res.data.intermediary.contact;
						that.pltel=res.data.intermediary.contactMobile;
						that.pldistrictCode=res.data.intermediary.districtCode;
						that.plprovince=res.data.intermediary.province;
						that.plcity=res.data.intermediary.city;
						that.pldistrict=res.data.intermediary.district;
						that.pladdr=res.data.intermediary.province+res.data.intermediary.city+res.data.intermediary.district;
						that.plnum=res.data.intermediary.num;
						that.plprice=res.data.intermediary.salary;
						that.plrequire=res.data.intermediary.requirement;
						that.filedata=res.data.intermediary.fileUrl;
					}
				}
			}).catch(function (error) {
				console.log(error);
			});
		},
		// 上传文件
		afterRead(event) {
			var file = event.target.files;
			var formData = new FormData()
			formData.append('file', file[0])
			Toast.success("上传中...");
			var that=this;
			this.$http.post('/common/uploadFile',formData).then(function (res) {
				console.log(3,res);
				if (res.code==10000) {
					Toast.success(res.msg);
					console.log(res)
					that.filedata=res.data.url
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		//删除文件
		del(){
			Dialog.confirm({
				title: '提示',
				message: '您要删除该文件吗？'
			}).then(() => {
				this.filedata=""
			}).catch(() => {
			});
		},
		//公司地址
		plconfirmarea(res){
			this.pladdr=res[0].name+res[1].name+res[2].name
			this.plprovince=res[0].name;
			this.plcity=res[1].name;
			this.pldistrict=res[2].name;
			this.pldistrictCode=res[2].code;
			this.plshow = false;
		},
		//公司地址关闭
		plcancelarea(){
			this.plshow = false;
		},
		//提交数据
		sub(){
			var that=this;
			let test1 = /^1[3-9][0-9]{9}$/
			if (!test1.test(this.pltel)) {
				Toast.fail("请填写正确格式的手机号码");
				return false;
			}
			this.$http.post('/main/intermediaryRegister',{
				name:that.plenterprise,
				province:that.plprovince,
				city:that.plcity,
				district:that.pldistrict,
				districtCode:that.pldistrictCode,
                contact:that.plname,
                contactMobile:that.pltel,
                num:that.plnum,
                salary:that.plprice,
                requirement:that.plrequire,
                fileUrl:that.filedata,
			}).then(function (res) {
				if (res.code==10000) {
					Dialog.alert({
						title: '提示',
						message: '您已登记成功，我们会尽快与您联系，请保持手机畅通！',
						confirmButtonText:'我知道了'
					}).then(() => {
						that.$router.push({
							path:"/home01"
						})
					});						
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		}
	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	position: relative;
	.cc-jieshao{
		position: absolute;
		right: 0;
		top:180px;
		width: 67px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		background:linear-gradient(319deg,rgba(48,114,252,1) 0%,rgba(98,177,255,1) 100%);
		border-radius:15px 0px 0px 15px;
		font-size:11px;
		color:rgba(255,255,255,1);
	}
}
.cc-logo{
	width:375px;
	height:207px;;
}
.cc-logo1{
	width:375px;
	height:40px;
}
.cc-register{
	padding: 19px 10px;
	background:#1E97A8;
	.cc-register1{
		padding:3px 16px 5px;
		background:#fff;
		border-radius:4px;
		margin-bottom: 20px;
		.cc-input0{
			padding-left: 5px;
			border-bottom: 1px solid #eee;
			position: relative;
			&::after{
				content: '*';
				position: absolute;
				left: 0;
				top: 22px;
				color:red;
			}
		}
		.cc-tit{
			padding:25px 0 14px;
			font-size:18px;
			font-weight:500;
			color:rgba(17,26,52,1);
			line-height:18px;
		}
		.cc-tit1{
			padding:18px 0;
			font-size:16px;
			color:rgba(17,26,52,1);
			line-height:22px;
		}
		.cc-input{
			line-height:32px;
			padding:13px 6px 12px;
			font-size:16px;
			color:rgba(17,26,52,1);
		}
		.cc-upload1{
			padding-bottom: 27px;
			.cc-uploaded{
				width:95px;
				height:95px;
				border-radius:2px;
				border:1px solid #eee;
				position: relative;
				img{
					width:95px;
					height:95px;
				}
				.cc-del{
					position: absolute;
					right: 0;
					top:0;
					width: 20px;
					height: 20px;
				}
			}
			.cc-uploadicon{
				width:95px;
				height:95px;
				border-radius:2px;
				border:1px solid #eee;
				text-align: center;
				padding-top:26px;
				position: relative;
				.cc-icon{
					font-size:28px;
					color:rgba(133,139,156,1);
				}
				p{
					font-size:12px;
					color:rgba(133,139,156,1);
					line-height:16px;
				}
				input{
					position: absolute;
					left: 0;
					top:0;
					width:95px;
					height:95px;
					opacity: 0;
				}
			}
		}

	}
	.cc-button{
		width:343px;
		height:47px;
		line-height:47px;
		text-align: center;
		color:#fff;
		font-size:18px;
		font-weight:500;
		background:#F0C01E;
		border-radius:2px;
		margin: 0 auto 20px;
		&.b2{
			background:#1C5A63;
		}
	}
}
</style>